import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const router = new Router({
  mode: "hash",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/entry",
      component: () => import("@/views/entry/EntryIndex"),
      children: [
        {
          name: "Login",
          path: "login",
          component: () => import("@/views/entry/Login")
        },
        {
          name: "Register",
          path: "register",
          component: () => import("@/views/entry/Register")
        }
      ]
    },
    {
      path: "/",
      component: () => import("@/views/dashboard/Index"),
      children: [
        // Dashboard
        {
          name: "Dashboard",
          path: "",
          component: () => import("@/views/dashboard/Dashboard")
        },
        {
          name: "Monitor",
          path: "monitor",
          component: () => import("@/views/dashboard/Monitor")
        },
        {
          name: "AlarmCenter",
          path: "alertnotif",
          component: () => import("@/views/dashboard/AlertCenter")
        },
        {
          name: "AiPredict",
          path: "aipredict",
          component: () => import("@/views/dashboard/AiPredict")
        },
        {
          name: "Active Maint",
          path: "activemaint",
          component: () => import("@/views/dashboard/ActiveMaint")
        },
        //EChart1
        {
          name: "EChart Demo1",
          path: "chartdemo/echart1",
          component: () => import("@/single/echart1")
        },
        //DemoWizard
        {
          name: "功能演示",
          path: "chartdemo/demowizard",
          component: () => import("@/single/demowizard"),
          meta: { requireAuth: false }
        },

        // Pages
        {
          name: "User Profile",
          path: "user",
          component: () => import("@/views/dashboard/basic/UserProfile")
        },
        {
          name: "#101号风机",
          path: "windno/wind101",
          component: () => import("@/views/dashboard/WindNo1")
        },
        {
          name: "#102号风机",
          path: "windno/wind102",
          component: () => import("@/views/dashboard/WindNo2")
        }
      ]
    },
    {
      path: "*",
      component: () => import("@/views/entry/EntryIndex"),
      children: [
        {
          name: "404 Error",
          path: "",
          component: () => import("@/views/entry/Error")
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next) => {
  const token = window.localStorage.getItem("token");
  if (
    to.matched.some(record => record.meta.requireAuth) &&
    (!token || token === null)
  ) {
    next({
      path: "entry/login",
      query: { redirect: to.fullPath }
    });
  } else if (token && to.name == "Login") {
    // 用户已登录，但又去访问登录页面时不让他过去
    next({
      path: from.fullPath
    });
  } else {
    next();
  }
});

export default router;
