<template>
  <v-card class="v-card--wizard" elevation="1" max-width="1000">
    <v-card-title class="justify-center display-2 font-weight-light pt-5">{{
      maintitle
    }}</v-card-title>

    <div class="text-center display-1 grey--text font-weight-light mb-6">
      {{ subtitle }}
    </div>

    <base-material-tabs
      v-model="internalValue"
      background-color="transparent"
      centered
      color="ndblue2"
      icons-and-text
      @click="$emit('change')"
    >
      <!--<v-tabs-slider class="mt-1" color="accent" />-->

      <v-tab
        v-for="(item, i) in items"
        :key="i"
        :ripple="true"
        :disabled="!availableSteps.includes(i)"
      >
        {{ item.text }}
        <v-icon v-text="item.icon" />
      </v-tab>
    </base-material-tabs>

    <div class="my-6" />

    <v-card-text>
      <v-tabs-items v-model="internalValue">
        <slot />
      </v-tabs-items>
    </v-card-text>

    <v-card-actions class="pb-4 pa-4">
      <v-btn
        :disabled="internalValue === 0"
        class="white--text"
        color="accent"
        min-width="125"
        @click="$emit('click:prev')"
        >上一步</v-btn
      >

      <v-spacer />

      <v-btn
        :disabled="!availableSteps.includes(internalValue + 1)"
        color="accent"
        min-width="100"
        @click="$emit('click:next')"
        >{{ internalValue === items.length - 1 ? "完成" : nextbtn }}</v-btn
      >
      <v-btn
        v-if="internalValue === items.length - 1"
        color="accent"
        min-width="100"
        >导出</v-btn
      >
    </v-card-actions>
  </v-card>
</template>

<script>
// Mixins
import Proxyable from "vuetify/lib/mixins/proxyable";

export default {
  name: "BaseShowWizard",

  mixins: [Proxyable],

  props: {
    maintitle: {
      type: String,
      default: "主动运维分析预测"
    },
    subtitle: {
      type: String,
      default: "流程演示"
    },
    nextbtn: {
      type: String,
      default: "下一步"
    },

    availableSteps: {
      type: Array,
      default: () => []
    },
    items: {
      type: Array,
      default: () => []
    }
  }
};
</script>

<style type="text/css">
.v-tabs--icons-and-text > .v-tabs-bar {
  min-height: 72px !important;
}
</style>
