import axios from "axios";

// 基础配置
axios.defaults.timeout = 5000; // 超时时间
axios.defaults.baseURL = "";

const axiosflask = axios.create({
  headers: {
    "content-type": "application/json;charset=UTF-8",
    token: "one"
  },
  baseURL: "http://localhost:5000/api/",
  timeout: 5000,
  withCredentials: false
});

const axiosboot = axios.create({
  headers: {
    "content-type": "application/json;charset=UTF-8"
  },
  baseURL: "http://localhost:8080",
  params: {},
  timeout: 5000,

  withCredentials: false
});

axiosboot.interceptors.request.use(
  config => {
    // 在发送请求之前做某事，比如说 设置token
    const token = window.localStorage.getItem("token");

    config.headers["Authorization"] = "Bearer" + token;
    config.params = {
      // add your default ones
      //token: token,
      // spread the request's params
      ...config.params
    };
    return config;
  },
  error => {
    // 请求错误时做些事
    if (error.response != undefined) {
      return Promise.reject(JSON.stringify(error.response.data.message));
    } else {
      return Promise.reject(JSON.stringify("Internal Error:" + error.message));
    }
  }
);

axiosboot.interceptors.response.use(
  response => {
    // 对响应数据做些事
    if (response.status === 200) {
      if (response.data) {
      } else {
      }
    }
    return response;
  },
  error => {
    if (error.response != undefined) {
      return Promise.reject(JSON.stringify(error.response.data.message));
    } else {
      return Promise.reject(JSON.stringify("Internal Error:" + error.message));
    }
  }
);

export default axiosboot;
