<template>
  <v-card v-bind="$attrs" :class="classes" :id="identify" class="v-card--data pa-3">
    <div class="d-flex grow flex-wrap">
      <v-sheet
        :color="color"
        :max-height="90"
        :width="'50%'"
        elevation="6"
        class="pa-7 text-start v-card--data__heading mb-n6"
        dark
      >
        <v-icon size="32" v-text="icon" />
        <span v-if="title" class="display-1 font-weight-light px-3" v-text="title"></span>

        <slot slot="reveal-actions" name="reveal-actions" />
      </v-sheet>

      <v-col v-if="hoverReveal" cols="12" class="px-2 py-0 mt-n12">
        <slot name="reveal-actions" />
      </v-col>
    </div>

    <slot name="datacontent" />

    <template v-if="$slots.actions">
      <v-divider class="mt-2" />

      <v-card-actions class="pb-0">
        <slot name="actions" />
      </v-card-actions>
    </template>
  </v-card>
</template>

<script>
export default {
  name: "DataCard",

  data() {
    return {
      identify: "defaultid"
    };
  },

  props: {
    color: {
      type: String,
      default: "success"
    },
    hoverReveal: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      default: undefined
    },
    title: {
      type: String,
      default: ""
    }
  },

  computed: {
    classes() {
      return {
        "v-card--data--has-heading": true,
        "v-card--data--hover-reveal": this.hoverReveal
      };
    }
  }
};
</script>

<style lang="sass">
.v-card--data__heading
    max-height: 185px

.v-card--data
    &__heading
        position: relative
        top: -70px
        transition: .3s ease
        z-index: 1

    &.v-card--data--hover-reveal:hover .v-card--data__heading
        transform: translateY(-40px)
</style>
